html,body{font-family:Helvetica Neue, Helvetica, Arial, sans-serif;margin:0;padding:0;color:#333;-webkit-user-select:none;background-color:#efefef;-webkit-font-smoothing:antialiased}*,*:hover,*:active,::before,::after{box-sizing:border-box;-webkit-tap-highlight-color:transparent}a:link,a:visited,a:hover,a:active{text-decoration:none;color:inherit;outline:0}button{height:48px;line-height:48px;border:none;font-size:15px;position:relative;padding:0;margin:0;background-color:transparent}button:focus{outline:0}input{margin-bottom:2px !important}input:focus,input,textarea,textarea:focus,select{box-shadow:none;border:0;background-color:white;padding:0;margin:0;font-size:16px;outline:0}input,select{text-align:right;-webkit-appearance:none}input{position:relative}select{direction:rtl}select:first-child{direction:ltr}textarea,textarea:focus{-webkit-box-flex:1;-ms-flex:1;flex:1;resize:none;min-height:70px;margin:10px 0}.fade-transition{-webkit-transition:.2s;transition:.2s;opacity:1}.fade-enter,.fade-leave{opacity:0;will-change:opacity}.pull-loading{height:60px;position:relative;margin-bottom:-5px;margin-top:-55px}.pull-loading>div{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:12px;color:#999}.dragging{will-change:transform}



